body, html {
  margin: 0;
  padding: 0;
  height: 100%;
}


.App {
  background-image: url("./imagenes/33141.jpg");
  background-size: cover;  /* la imagen cubra toda la pantalla */
  background-position: center;  /* Centra la imagen */
  background-repeat: no-repeat;  /* Evita que la imagen se repita */
  width: 45%; /* El 35% del ancho de la pantalla */
  height: 100%; /* Ocupa toda la altura de la pantalla */
  padding: 10px;
}

